import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import {Parallax, Pagination, Navigation } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import style from './Swiper.module.scss'


function Swap() {
  return (
    <>
      <Swiper
        style={{
          "--swiper-navigation-color": "#fff",
          "--swiper-pagination-color": "#fff",
          'borderRadius': 40,
        }}
        speed={600}
        parallax={true}
        pagination={{
          clickable: true
        }}
        navigation={true}
        modules={[Parallax, Pagination, Navigation]}
        className="mySwiper"
      >
        <div
          slot="container-start"
          className="parallax-bg"
          style={{
            "backgroundImage":
              "url(https://img.freepik.com/premium-vector/ethereum-cryptocurrency-digital-coin-for-presentation-computer-circuit-board-vector-blockchain_185386-777.jpg?w=360)"
          }}
          data-swiper-parallax="-23%"
        ></div>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
            Bitcoin
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
            :  
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper} >
              Bitcoin    ,  
                 .    
                  ,   
                      
               .
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
            Solana
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
            :     
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper} >
            Solana   ,       <br/>
            Solana    -    ,          DApps.
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
            Near
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
          :     
          </div>
          <div className="text" data-swiper-parallax="-100">
          <p className={style.textSwiper}>
            Near Protocol      -,        DApps    
            </p>
          </div>
        </SwiperSlide>
        
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
            Atom
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
          :  
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper}>
            Cosmos    ,    ,      .        (BFT).  BFT             .     ATOM           Cosmos Hub.
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
            Avalanche
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
          :     
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper}>
             Avalanche         .     :   (X-Chain),   (C-Chain)    (P-Chain). X-Chain      . C-Chain    -. P-Chain      .
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
          Polygon(MATIC)
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
          :    
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper}>
            Polygon        (L2)   .       Ethereum    .
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
          Chainlink(LINK)

          </div>
          <div className="subtitle" data-swiper-parallax="-200">
          :  ,  ,   
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper}>
            Chainlink    ,          -  .
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="title" data-swiper-parallax="-300">
            Ethereum
          </div>
          <div className="subtitle" data-swiper-parallax="-200">
          :  
          </div>
          <div className="text" data-swiper-parallax="-100">
            <p className={style.textSwiper}>
            Ethereum  -   ,      (ETH).   Ethereum       DeFi  NFT.
            </p>
          </div>
        </SwiperSlide>
      </Swiper>
    </>
  )
}

export default Swap;