import React,{useState} from 'react'
import { Card } from '../components/Card'
import axios from 'axios'
import {appContext} from '../App'
import { Link } from "react-router-dom";
export const Orders = () => {
    const {onAddToCard, onAddToFafourite} = React.useContext(appContext)
    const [orders, setorders] = useState([])
    
    const [isLoading, setIsLoading] = useState(true)

    React.useEffect( () => {
        (async() => {
            try{
                const { data }  = await axios.get('https://635934bbff3d7bddb99be8d1.mockapi.io/orders')
                setorders(data.map(obj => obj.items).flat())
                setIsLoading(false)
            } catch(error) {
                alert('   ')
                console.error(error)
            }
            /* console.log(data.reduce((prev, obj) => [...prev, ...obj.items], []).flat()) */

        })()
    }, [])
  
return (
        <div className="content">
        {
          orders.length > 0 ? (
            <>
            <div className='header'>
          <h2> </h2>
        </div>

      <div className="cards">
      {(isLoading ? [...Array(8)] : orders).map((item, index) => {
            return <Card 
                key={index}
                loading={isLoading}
                {...item}
            />
        })}
      </div></>
          ) : (
            (
              <div className='fafouriteIsNull'>
                <div className='center'>
                  <h2>   </h2>
                  <p style={{"textAlign": "center" }}> ? <br/>     .</p>
                  <Link to='/'>
                  <button  className="greenBotton">  <img src='img/errow-left.svg'/></button>
                  </Link>
                </div>
              </div>
            )
          )
        }
    </div>
  )
  
}
